.products {

  &__product-card {
    display: grid;
    grid-template-columns: 1fr;
    border: 1px solid transparent;
    border-radius: 4px;
    box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
  }

  &__product-card-image-wrapper {
    position: relative;
    padding-top: 92.857%;
  }

  &__product-card-image {
    position: absolute;
    top: 0;
    left: 0;
    max-width: 100%;
    height: auto;
    border-top-left-radius: 4px;
    border-top-right-radius: 4px;
  }

  &__product-card-info-wrapper {
    display: grid;
    grid-template-columns: 1fr;
    row-gap: 20px;
    padding: 6px;
  }

  &__product-card-name-price-wrapper {
    display: grid;
    grid-template-columns: 1fr;
    row-gap: 5px;
    justify-items: center;
  }

  &__product-card-price {
    font-size: 14px;
  }

  &__product-card-add-to-cart-wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}


